		},
		bindChange: function (e) {
		            const val = e.detail.value
		            // this.year = this.years[val[0]]
		            this.month = this.months[val[0]]
		            this.day = this.days[val[1]]
					this.hour = this.hours[val[2]]
		},
		// bindPickerChange(e){
		// 	// console.log('picker发送选择改变，携带值为', e.target.value);
		// 	// this.index = e.target.value
		// },
		// bindDateChange(e){
		// 	this.date = e.target.value;
		// },
		// timeChange(e) {
		// 	this.time = e.target.value;
		// },
		stopPrevent(){},
		getDate(type){
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			
			if(type === 'start'){
				year = year - 60;
			}else if(type === 'end'){
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`
		},
		changeClock(type){
			if(type === 'decrease'){
				if(this.serviceClock <= 1){
					uni.showToast({
						title: '不能再减少了哦',
						icon: 'none'
					})
				}else{
					this.serviceClock--; 
				}
			}else{
				this.serviceClock++;
			}
		},
		selectCoupon(index){
			this.disCount.account = this.couponList[index].price;
			this.disCount.text = `${this.couponList[index].title}${this.couponList[index].price}元` ;
			this.toggleMask('','coupon');
		},
		changeUseCoupon(){
			this.useCoupon = !this.useCoupon;
			this.toggleMask('','coupon');
		},
		toPay(){
			uni.redirectTo({
				url: `/pages/pay/pay?account=${this.realPayment}`
			})
		}
	}
